import { useRouter } from "next/router";

export default function PricingPlans() {
  const router = useRouter();

  const plans = [
    {
      title: "Daily Plan",
      price: "₹49",
      duration: "Per Day",
      features: ["1-Day Access", "Basic Support", "No Commitment"],
      link: "/checkout?plan=daily",
    },
    {
      title: "Monthly Plan",
      price: "₹499",
      duration: "Per Month",
      features: ["30-Day Access", "Priority Support", "Save 20%"],
      link: "/checkout?plan=monthly",
    },
    {
      title: "Yearly Plan",
      price: "₹4999",
      duration: "Per Year",
      features: ["365-Day Access", "24/7 Support", "Save 60%"],
      link: "/checkout?plan=yearly",
    },
  ];

  return (
    <div className="container mx-auto px-4 py-12">
      <h2 className="text-3xl font-bold text-center mb-10">Choose Your Plan</h2>
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-8">
        {plans.map((plan) => (
          <div
            key={plan.title}
            className="border rounded-2xl shadow-lg p-6 flex flex-col justify-between hover:shadow-xl transition duration-300"
          >
            <div>
              <h3 className="text-xl font-semibold mb-2">{plan.title}</h3>
              <p className="text-3xl font-bold text-blue-600 mb-2">{plan.price}</p>
              <p className="text-sm text-gray-500 mb-4">{plan.duration}</p>
              <ul className="space-y-2 mb-6">
                {plan.features.map((feature, i) => (
                  <li key={i} className="text-gray-700">✅ {feature}</li>
                ))}
              </ul>
            </div>
            <button
              onClick={() => router.push(plan.link)}
              className="mt-auto bg-blue-600 text-white py-2 rounded-xl hover:bg-blue-700 transition"
            >
              Buy Now
            </button>
          </div>
        ))}
      </div>
    </div>
  );
}
